.ShopPageComponent {
    display: flex;
    flex-direction: column;
    max-width: 100vw;
    margin-top: -15vh;
    max-height: 100vh;
    background-color: var(--red);
    align-items: center;
    overflow-y: scroll;
    overflow-x: hidden;
    scroll-behavior: smooth;
}
.filter-buttons-container {
    width: 85vw;
    display: flex;
    justify-content: space-evenly;
    margin-top: 25vh;
    margin-bottom: 5vh;
    align-self: flex-end;

}
.filterButtons p {
    margin: 0;
    font-family: "A Box For 1";
    font-size: min(7.5vw, 5em);
}
.filterButtons p:hover {
color: var(--white);

}
.activeSort {
    background-color: var(--black);
}
.activeSort p {
    font-family: "A Box For 2";
    color: var(--red);
}
.product-list-container {
    display: flex;
    width: 85vw;
    flex-wrap: wrap;
    align-items: flex-start;
    justify-content: space-evenly;
    align-self: flex-end;
}
.spacer {
    min-height: calc(max(30vh, 18rem));
    width: 100vw;
    background-color: var(--red);
}

/* SOMETHING WITH TRANSITIONS */

@media only screen and (max-height: 960px) {
    .filter-buttons-container {
        margin-top: calc(9rem + 10vh);
    }
    .ShopPageComponent {
        margin-top: -9rem;
    }


}